import React, { Component, Fragment } from 'react';
import { Badge, Container, Row, Col, Collapse, Modal, ModalBody, Button } from 'reactstrap';

import Icon from '@mdi/react';
import { mdiCashMultiple, mdiChevronLeft, mdiCurrencyBtc, mdiHeart } from '@mdi/js';
import { local_urls } from '../layout/default/urls';
import { HashLink } from 'react-router-hash-link';
import { payaddr } from './data/payment';
import QRious from 'qrious';

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            method: ""
        };
    }

    onModal = function (item) {
        this.setState({ popUpItem: item, popUp: true })
    }.bind(this)

    render() {
        var methods;
        var qrs = {};

        if (typeof (this.state.popUpItem) === 'object') {
            for (var i = 0; i < this.state.popUpItem["address"].length; i++) {
                var qr = new QRious(
                    {
                        value: this.state.popUpItem["address"][i].addr,
                        size: 180
                    }
                );

                qrs[this.state.popUpItem["address"][i].addr] = qr.toDataURL('image/jpeg');
            }
        }

        return (
            <div className={`donate ${this.state.method ? this.state.method : ""}`}>
                <Container className="donate-body">
                    <p className="donate-txt-body pt-5 pb-5 mb-0 text-center">
                        <HashLink className="back-to-page" to="/">
                            <Icon path={mdiChevronLeft} size={0.9} />
                            Back to website
                        </HashLink>
                        <div className="logo">
                            <img src={process.env.PUBLIC_URL + '/assets/radovan-pranda-logo.jpg'} />
                        </div>

                        <h1 align="center">Donate</h1>
                        <div className="mt-2 mb-1 methods">
                            <h5>What do you prefer?</h5>
                            <a class="link-donate-method" onClick={() => { this.setState({ method: "money" }) }}>
                                <Icon path={mdiCashMultiple} size={1.1} />
                                Money
                            </a>

                            <a class="link-donate-method" onClick={() => { this.setState({ method: "crypto" }) }}>
                                <Icon path={mdiCurrencyBtc} size={1.1} />
                                Cryptocurrencies
                            </a>
                        </div>

                        <Collapse isOpen={this.state.method}>
                            <div className="pay-methods">
                                <h5>Select method</h5>
                                <Collapse isOpen={this.state.method === "money"}>
                                    <div class="link-donate-pay-method paypal">
                                        <svg style={{ width: "1.25rem", height: "1.25rem", fill: "#00489b" }} x="0px" y="0px" viewBox="0 0 512 512" >
                                            <g>
                                                <g>
                                                    <path d="M409.713,38.624C385.842,11.584,342.642,0,287.377,0H126.993c-11.296,0-20.896,8.16-22.688,19.2L37.522,439.392
c-1.312,8.288,5.152,15.776,13.6,15.776h99.008l24.864-156.48l-0.768,4.928c1.76-11.04,11.328-19.2,22.624-19.2h47.04
c92.448,0,164.8-37.248,185.952-144.992c0.64-3.2,1.632-9.344,1.632-9.344C437.489,90.208,431.441,63.168,409.713,38.624z"/>
                                                </g>
                                            </g>
                                            <g>
                                                <g>
                                                    <path d="M456.529,150.496c-22.976,106.08-96.288,162.208-212.64,162.208h-42.176L170.225,512h68.416
c9.888,0,18.304-7.136,19.84-16.832l0.8-4.224l15.744-98.912l1.024-5.44c1.536-9.696,9.952-16.832,19.808-16.832h12.512
c80.864,0,144.16-32.576,162.656-126.816C478.449,205.12,474.865,173.408,456.529,150.496z"/>
                                                </g>
                                            </g>
                                        </svg>
                                        <div className="title">
                                            PayPal
                                        </div>
                                        <div className="btn-select">
                                            <a className="link-donate-method" href={`${local_urls["paypal"]}`}>Donate</a>
                                        </div>
                                    </div>
                                </Collapse>
                                <Collapse isOpen={this.state.method === "crypto"}>
                                    {
                                        payaddr.map((item, key) => {
                                            return (
                                                <div key={key} className={`link-donate-pay-method ${item.id}`}>
                                                    <img src={item.ico} />

                                                    <div className="title">
                                                        {item.name}
                                                    </div>
                                                    <div className="btn-select">
                                                        <a className="link-donate-method" onClick={() => { this.onModal(item) }} >Donate</a>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }

                                </Collapse>


                            </div>
                            {
                                (this.state.method === "money")
                                ?<div className="terms mt-2">
                                    I am committed to your privacy. Your payment details may be processed by a PayPal company (for credit/debit cards) or PayPal,
                                    and a record of your donation will be also stored by me.<br />
                                    By donating money, you confirm that you have decided to donate money freely and that no product or service is being purchased or pre-purchased.
                                    Terms and the relationship shall be governed by and construed in accordance with the laws of Slovak republic, without regard to its principles
                                    of conflict of laws.
                                </div>
                                :null
                            }
                            
                        </Collapse>
                    </p>

                </Container>
                <Modal size="lg" isOpen={this.state.popUp} centered={true}>
                    <ModalBody className="text-center">
                        <h3 className="mb-4">
                            {(typeof (this.state.popUpItem) === 'object') ? this.state.popUpItem.name : ""}
                        </h3>
                        <Row>
                            {
                                Object.keys(qrs).map(
                                    (key) => {
                                        return <Col>
                                            <img src={qrs[key]} />
                                            <p className="pt-3 text-center">{key}</p>
                                        </Col>;
                                    }
                                )
                            }
                        </Row>
                        <div className="pb-1 pt-1">
                            <Button color="secondary" onClick={() => this.setState({ popUpItem: undefined, popUp: false })}>Close</Button>
                        </div>
                    </ModalBody>
                    <ModalBody className="bg-dark text-white text-center">
                        Thank you very much for your donation. <Icon path={mdiHeart} size={1.0} color="#ff1744" />
                    </ModalBody>
                </Modal>
            </div>
        );

    }
}